import Link from "next/link";
import BaseLayout from '../components/Base-Layout';
import Footer from "../components/Footer";

export default function Index() {
    return (
        <div>
            <BaseLayout>

                

            </BaseLayout>

            <style jsx>{`
                .container-logos a {
                    display: block;
                    position: relative;
                }
                .container-logos {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    height: 400px;
                    margin-top: 15%;
                }
                .fundamental {
                    border-top: 1px solid #d3d3d3;
                    border-right: 1px solid #d3d3d3;
                    border-radius: 22px;
                    // display: flex;
                    // align-items: center;
                    // justify-content: center;
                    position: relative;
                    width: 25em;
                    height: 18em;

                    -webkit-box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                    -moz-box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                    box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                }
                .sun-logo {
                    position: absolute;
                    width: 400px;
                    max-width: 100%;
                }
                .eja {
                    border-top: 1px solid #d3d3d3;
                    border-right: 1px solid #d3d3d3;
                    border-radius: 22px;
                    position: relative;
                    padding: 1.5em;
                    width: 25em;
                    height: 18em;

                    -webkit-box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                    -moz-box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                    box-shadow: -11px 12px 12px -5px rgba(171,171,171,1);
                }
                .logo-eja {
                    position: absolute;
                    width: 400px;
                    max-width: 100%;
                    top: 15px;
                }
            `}</style>
        </div>
    );
}